<template>
	<view class="record-info" v-if="_show">
		<uni-popup ref="popup">
			<view class="flex head">
				<view class="left">
					<view>
						<image src="" mode="widthFix"></image>
					</view>
					<text>小米</text>
				</view>
				<view class="times">
					共访问了你15次
				</view>
				<view class="close"></view>
			</view>
			<view class="list">
				<view class="item" v-for="(item,index) in list" :key="index">
					<view>{{item.time}} 访问了你</view>
					<view v-if="item.tel" class="tel">
						留下了手机号：
						{{item.tel}}
					</view>
				</view>
			</view>
		</uni-popup>
	</view>
</template>

<script>
	export default {
		props: ["id", "show"],
		data() {
			return {
				list: [{
						time: "2020.07.26 17:42",
						tel: ""
					},
					{
						time: "2020.07.26 17:42",
						tel: "15280511236"
					},
					{
						time: "2020.07.26 17:42",
						tel: ""
					}
				]
			};
		},
		watch: {
			show(val) {
				if (val) {
					this.$refs.popup.open();
				}
			}
		},
		computed: {
			_show: {
				get: function() {
					return this.show;
				},
				set: function(val) {
					this.$emit("update:show", val);
				},
			},
		}
	}
</script>

